import BASE_PATH from '@/api/basePath'
const API = {
  LOGIN (context, params) {
    return context.$http.post(BASE_PATH + 'zhfwgl/leave/appUserLoginAction!appUserLogin.do', params)
  },
  LOGOUT (context, params) {
    return context.$http.post(BASE_PATH + 'zhfwgl/leave/appUserLoginAction!appUserLogout.do', params)
  },
  CHECKTOKEN (context, params) {
    return context.$http.post(BASE_PATH + 'zhfwgl/leave/appUserLoginAction!checkLoginState.do', params)
  }
}

export default API



