export default {
  name: 'App',
  data () {
    return {
      transitionName: ''
    }
  },
  watch: {
    $route (to, from, next) {
      this.$store.commit('setRranisitionName', {
        toName: to.name,
        fromName: from.name
      })
      this.transitionName = this.$store.getters.getTransitionName
    }
  }
}



