import Vue from 'vue'
import Router from 'vue-router'
import mineRoutes from './mine'
import vacationRoutes from './vacation'
import attendanceRoutes from './attendance'
import { Indicator, MessageBox } from 'mint-ui'
import API from '@/api/login'
Vue.use(Router)

const _routes = [
  {
    path: '/',
    name: 'index',
    component: resolve => require(['@/pages/index'], resolve),
    children: [
      {
        path: '/',
        name: 'home',
        component: resolve => require(['@/pages/home'], resolve)
      },
      {
        path: '/attendance',
        name: 'attendance',
        component: resolve => require(['@/pages/attendance'], resolve)
      },
      {
        path: '/leave',
        name: 'leave',
        component: resolve => require(['@/pages/leave'], resolve),
        meta: { requireslogin: false }
      },
      {
        path: '/mine',
        name: 'mine',
        component: resolve => require(['@/pages/mine'], resolve)
        // meta: { requireslogin: true }
      }
    ]
  },
  {
    path: '/login',
    name: 'login',
    component: resolve => require(['@/pages/login/login'], resolve)
  },
  {
    path: '/checkTab',
    name: 'checkTab',
    component: resolve => require(['@/pages/vacation/checkTab'], resolve)
  }

]
let routes = _routes.concat(mineRoutes).concat(attendanceRoutes).concat(vacationRoutes)
const router = new Router({
  routes
})

router.beforeEach((to, from, next) => {
  let vm = router.app
  let user = vm.$options.store.getters.getUser
  let token = user.token
  if (token !== '' && token !== undefined) {
    Indicator.open({
      spinnerType: 'fading-circle'
    })
    // 验证验证token是否过期
    API.CHECKTOKEN(vm, { token }).then((rep) => {
      Indicator.close()
      let result = rep.body
      if (result.success) {
        next()
      } else {
        if (to.name === 'login') {
          next()
        } else {
          MessageBox.alert(result.message, '操作提示').then(action => {
            vm.$router.push({ name: 'login' })
          })
        }
      }
    }, (rep) => {
      Indicator.close()
      next({ name: from.name })
    })
  } else {
    if (to.name === 'login') {
      next()
    } else {
      vm.$router.push({ name: 'login' })
    }
  }
})

export default router



