const vacationRoutes = [
  {
    path: '/applyVacationDept',
    name: 'applyVacationDept',
    component: resolve => require(['@/pages/vacation/applyVacationDept'], resolve),
    meta: { requireslogin: false }
  },
  {
    path: '/applyVacationWindow',
    name: 'applyVacationWindow',
    component: resolve => require(['@/pages/vacation/applyVacationWindow'], resolve),
    meta: { requireslogin: false }
  },
  {
    path: '/vacationList',
    name: 'vacationList',
    component: resolve => require(['@/pages/vacation/vacationList'], resolve),
    meta: { requireslogin: false }
  },
  {
    path: '/checkList',
    name: 'checkList',
    component: resolve => require(['@/pages/vacation/checkList'], resolve),
    meta: { requireslogin: false }
  },
  {
    path: '/vacationCheckWindow',
    name: 'vacationCheckWindow',
    component: resolve => require(['@/pages/vacation/vacationCheckWindow'], resolve),
    meta: { requireslogin: false }
  },
  {
    path: '/vacationCheckDept',
    name: 'vacationCheckDept',
    component: resolve => require(['@/pages/vacation/vacationCheckDept'], resolve),
    meta: { requireslogin: false }
  }
]
export default vacationRoutes



