import Vuex from 'vuex'
import Vue from 'vue'
import { Indicator, MessageBox } from 'mint-ui'
import API from '@/api/login'

Vue.use(Vuex)

// 用户状态管理
const userModel = {
  state () {
    return {
      userData: {
        token: '', // 服务器返回凭据 判断用户的登录状态 和服务器约定超时时间
        data: {}
      },
      menuType: '', // 菜单类型（01 窗口请假  02 窗口审批  03 审批局请假  04 审批局审批）
      isChecked: false, // 审核（true） 未审核（false）
      vacationId: '' // 请假id
    }
  },
  getters: {
    getUser (state) {
      let user = state.userData
      if (!state.userData.token) {
        user = JSON.parse(localStorage.getItem('user') || '{}')
      }
      return user
    },
    getMenuType (state) {
      return state.menuType
    },
    getIsChecked (state) {
      return state.isChecked
    },
    getVacationId (state) {
      return state.vacationId
    }
  },
  mutations: {
    login (state, user) {
      state.userData.token = user.token
      state.userData.data = user.data
      Vue.set(state, 'userData', { token: user.token, data: user.data }) // 更新getters
    },
    loginout (state) {
      state.userData.token = ''
      localStorage.setItem('user', '')
      Vue.set(state, 'userData', { token: '', data: {} })// 更新getters
    },
    setMenuType (state, type) {
      state.menuType = type.data
    },
    setIsChecked (state, isChecked) {
      state.isChecked = isChecked.data
    },
    setVacationId (state, vacationId) {
      state.vacationId = vacationId.data
    }
  },
  actions: {
    login (context, vm) {
      let { loginid, password, from, loginIp, model } = vm
      console.log(vm)
      Indicator.open({
        text: '登录中...',
        spinnerType: 'fading-circle'
      })
      // 传入极光推送的需要的app设备号
      let registrationId = context.getters.getRegistrationId
      API.LOGIN(vm, { loginid, password, registrationId, loginIp, model }).then((rep) => {
        Indicator.close()
        let result = rep.body
        if (result.success) {
          let user = {
            token: result.token,
            data: result.date
          }
          localStorage.setItem('user', JSON.stringify(user))
          context.commit('login', user)
          MessageBox.alert(result.message, '操作提示').then(action => {
            if (from === '' || !from) {
              vm.$router.push({ name: 'home' })
            } else if (from === 'mine') {
              vm.$router.push({ name: 'mine' })
            } else {
              vm.$router.go(-1)
            }
          })
        } else {
          MessageBox.alert(result.message, '操作提示')
        }
      }, (rep) => {
        Indicator.close()
      })
    },
    loginout (context, vm) {
      let token = context.getters.getUser.token
      Indicator.open({
        text: '正在退出登录...',
        spinnerType: 'fading-circle'
      })
      API.LOGOUT(vm, { token }).then((rep) => {
        Indicator.close()
        let result = rep.body
        if (result.success) {
          context.commit('loginout')
          MessageBox.alert(result.message, '操作提示').then(() => {
            vm.$router.push({ name: 'login' })
          })
        } else {
          MessageBox.alert(result.message, '操作提示')
        }
      }, (rep) => {
        Indicator.close()
        MessageBox.alert('退出登录失败', '操作提示')
      })
    }
  }
}

const transitionModel = {
  state () {
    return {
      transitionName: '',
      routerHistoryArr: []
    }
  },
  getters: {
    getTransitionName (state) {
      return state.transitionName
    },
    getRouterHistoryArr (state) {
      return state.routerHistoryArr
    }
  },
  mutations: {
    setRranisitionName (state, { toName, fromName }) {
      if (state.routerHistoryArr.indexOf(toName) < 0) {
        state.routerHistoryArr.push(toName)
      } else {
        if (state.routerHistoryArr.indexOf(toName) > state.routerHistoryArr.indexOf(fromName)) {
          state.transitionName = 'slide-right'
        } else {
          state.transitionName = 'slide-left'
        }
      }
      if (!fromName) {
        state.transitionName = ''
      }
      return state.transitionName
    }
  }
}
// 设备ID
const registrationIdModel = {
  state () {
    return {
      registrationId: '' // 设备id
    }
  },
  getters: {
    getRegistrationId (state) {
      return state.registrationId
    }
  },
  mutations: {
    setRegistrationId (state, registrationId) {
      state.registrationId = registrationId
    }
  }
}
// 消息的数目
const messageNumModel = {
  state () {
    return {
      msgNum: 0 // 推送消息的数目
    }
  },
  getters: {
    getMsgNum (state) {
      return state.msgNum
    }
  },
  mutations: {
    setMsgNum (state, msgNum) {
      state.msgNum = msgNum
    }
  }
}
const store = new Vuex.Store({
  modules: {
    transitionModel,
    userModel,
    registrationIdModel,
    messageNumModel
  }
})
export default store



