const attendanceRoutes = [
  {
    path: '/clockIn',
    name: 'clockIn',
    component: resolve => require(['@/pages/attendance/clockIn'], resolve)
  },
  {
    path: '/clockInRecord',
    name: 'clockInRecord',
    component: resolve => require(['@/pages/attendance/clockInRecord'], resolve)
  }
]
export default attendanceRoutes



