

import Vue from 'vue'
import App from './App'
import router from './router'
import store from './store'
import VueResouce from 'vue-resource'
import LoadingPlugin from 'vux/src/plugins/loading/index.js'

import MintUI from 'mint-ui'
import 'mint-ui/lib/style.css'
import 'lib-flexible/flexible.js'
import fastclick from 'fastclick'

fastclick.attach(document.body)

Vue.config.productionTip = false
Vue.use(LoadingPlugin)
Vue.use(VueResouce)
Vue.use(MintUI)
Vue.use(VueResouce)

Vue.http.interceptors.push((request, next) => {
request.credentials = true
request.emulateJSON = true
request.timeout = 30000 

let timeout

if (request.timeout) {
timeout = setTimeout(() => {

request.abort()
}, request.timeout)
}
next((rep) => {
clearTimeout(timeout)
let result = rep.body
if (rep.status === 0) {
MintUI.MessageBox.alert('请求失败', '操作提示')
}
if (!result.success) { // 对token失效进行所有拦截
if (result.code && result.code === '12') {
MintUI.MessageBox.alert(result.message, '操作提示').then(() => {
router.push({ name: 'login' })
})
} else {
return rep
}
} else {
return rep
}
})
})
/* eslint-disable no-new */

window.apiready = function () {
initPush()
new Vue({
el: '#app',
router,
store,
components: { App },
template: '<App/>'
})
}










function initPush () {
let ajpush = window.api.require('ajpush')

ajpush.init(function (ret) {
if (ret && ret.status) {
}
})
ajpush.getRegistrationId(function (ret) {
var registrationId = ret.id
window.api.toast({
msg: 'app初始化成功'
})
store.commit('setRegistrationId', registrationId)
})
var param = {
alias: 'public'
}

ajpush.bindAliasAndTags(param, function (ret) {
let statusCode = ret.statusCode
if (statusCode === 0) {
window.api.toast({
msg: '初始化极光成功'
})
}
})
ajpush.setListener(function (ret) {
let extra = ret.extra

let msgcount = parseInt(extra.msg_noread_count)
store.commit('setMsgNum', msgcount)
}
)

window.api.addEventListener({
name: 'appintent'
}, function (ret, err) {
if (ret && ret.appParam.ajpush) {
var _ajpush = ret.appParam.ajpush
var extra = _ajpush.extra

let msgcount = parseInt(extra.msg_noread_count)
store.commit('setMsgNum', msgcount)
}
})

window.api.addEventListener({
name: 'noticeclicked'
}, function (ret, err) {
if (ret && ret.value) {
var _ajpush = ret.value
var extra = _ajpush.extra

let msgcount = parseInt(extra.msg_noread_count)
store.commit('setMsgNum', msgcount)
}
})
window.api.addEventListener({ name: 'pause' }, function (ret, err) {
ajpush.onPause()
})

window.api.addEventListener({ name: 'resume' }, function (ret, err) {
ajpush.onResume()
ajpush.setBadge({
badge: 0
})
})
}



